@use "global" as *;

.tab-contents-information {
}

.tab-contents-information__tab {
  display: flex;
  gap: rem(20);
  justify-content: center;
}

.tab-contents-information__item {
  width: 27%;
  font-size: rem(14);
  font-weight: $regular;
  background-color: $green-light;
  color: $green-accent;
  cursor: pointer;
  filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25));
  padding: rem(10) 0;
  text-align: center;
}

.tab-contents-information__item.current {
  color: $white;
  background-color: $green-accent;
}

.tab-contents-information__contents-container {
  margin-top: rem(16);
}

.js-information-content:not(:first-child) {
  display: none;
}

.tab-contents-information__content {
  border: solid 1px $green-accent;
  padding: rem(24);
  @include mq("md") {
    display: flex;
    gap: rem(48);
    align-items: center;
  }
}

.tab-contents-information__content:not(:first-child) {
}

.tab-contents-information__body {
  @include mq("md") {
    //width: rem(492);
    width: 48%;
  }
}

.tab-contents-information__title {
}

.tab-contents-information__text {
  margin-top: rem(8);
}

.tab-contents-information__img {
  margin-top: rem(8);
  @include mq("md") {
    //width: rem(492);
    width: 48%;
  }
}
