@use 'global' as *;

.qa-box {
  margin-top: rem(40);
}

.qa-box__question {
  background-color: $green-accent;
  color:$white;
  font-family: $sans;
  font-weight: $regular;
  font-size: rem(16);
  padding: rem(16);
  position: relative;
  @include mq('md') {
    font-size: rem(20);
    padding: rem(25) rem(40);
  }
}

.qa-box__answer {
  padding: rem(24) rem(16);
  border: solid 1px $green-accent;
  display: none;
  @include mq('md') {
    margin-top: rem(16);
    padding: rem(40);
  }
}

.qa-box__question.is-open .qa-icon__vertical{
  transform: rotate(90deg);
  transition: 0.5s;
}

